@use 'colors' as c;
@use 'fonts' as f;

html { height:100% } 

body {
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 100%;
}

$horizontal-padding: 20px;

$header-height: 100px;
$menu-horizontal-margin: 20px;

#main-header {
  grid-row: 1;
  min-height: $header-height;
  padding: 0px $horizontal-padding;
  display:flex;
  align-items: center;

  background-color: c.$tb-green;

  $logo-size: 70px;

  .header-logo {
    width: $logo-size;
    height: $logo-size;
    margin-right: $menu-horizontal-margin;
  }

  span.header-title {
    font-family: f.$font-heavy;
    color: c.$tb-yellow;
    text-transform: uppercase;
    font-size: 36px;
    display: block;
    line-height: 0.9;
  }

  #main-menu {

    margin-left: auto;
    margin-right: $menu-horizontal-margin;

    %menu-item-format {
      font-family: f.$font-heavy;
      text-transform: uppercase;
      text-decoration: none;
    
      &:hover {
        text-decoration: underline;
      }
    }

    #main-menu-list {
      
      display: flex;

      .main-menu-text {
        @extend %menu-item-format;
      }

      .sub-menu-text {
        @extend %menu-item-format;
      }
    }

    li {
      list-style-type: none;
    }
  }

  @media screen and (min-width:800px) {

    #menu-toggle, .toggle-open, .toggle-closed {display: none !important;}

    #main-menu {

      #main-menu-list {
        flex-direction: row;

        .main-menu-item {

          border-right: solid 2px c.$tb-yellow;
          padding: 0px 10px;
          margin: 10px 0px;
          position: relative;

          &:last-child {
            border-style: none;
          }

          .main-menu-text {
            color: c.$tb-yellow;
            font-size: 26px;
            line-height: 0.7;
          }

          &:hover {
            .sub-menu-list {
              display: block;
            }
          }

          .sub-menu-list {
            position: absolute;
            display: none;
            z-index: 1;
            min-width: 100%;
            background-color: transparent;
            left: 0;
            padding: 10px 0;

            &:hover {
              display: block;
            }

            .sub-menu-item {

              display: block;
              background-color: c.$tb-yellow;
              min-width: 100%;

              $padding: 0.6rem;
              padding: 0 $padding;
              padding-bottom: $padding;

              &:first-child {
                padding-top: $padding;
              }

              .sub-menu-text {
                color: c.$tb-green;
                font-size: 22px;
                line-height: 0.7;
              }
            }
          }  
        }
      }
    }
  }

  @media screen and (max-width: 799px) {

    #menu-toggle:checked {

      ~ label {
        &.toggle-open { display: block; }
        &.toggle-closed { display: none; }

        ~ ul#main-menu-list {
          display: block;
        }
      }
    }

    #menu-toggle {
      display: none;

      ~ label {
        color: c.$tb-yellow;
  
        &.toggle-open { display: none; }
        &.toggle-closed { display: block; }

        $icon-size: 45px;

        icon svg {
          width: $icon-size;
          height: $icon-size;
        }
      }

      ~ #main-menu-list {
        display: none;
        position: absolute;
        top: $header-height;
        right: 0px;
        background-color: c.$tb-blue;
        width: 100%;
        margin: 0px;
        padding: 20px 0px;

        .main-menu-item {          
          margin-left: 10px;
          font-size: 22px;

          &:not(:last-child) {
            margin-bottom: 5px;
          }

          .main-menu-text {
            color: c.$tb-yellow;
          }

          .sub-menu-list {
            padding-left: 20px;
            color: white;

            .sub-menu-item {
              .sub-menu-text {
                color: c.$tb-yellow;
              }
            }
          }
        }        
      }
    }
  }
}

$horizontal-padding: 20px;

#main-content {
  grid-row: 2;
  padding: 40px 40px;
}

$footer-height: 50px;

#main-footer {
  grid-row: 3;
  min-height: 50px;
  color: white;
  font-size: 0.9rem;
  background-color: c.$tb-blue;
  padding: 10px $horizontal-padding;

  display: grid;
  grid-template-columns: 2fr, 1fr, 2fr;
  align-items: center;

  .tb-legal {
    grid-column: 1;
    grid-row: 1;
    justify-self: start;
  }  
  
  a {
    color: white;
    text-decoration: none;
  }
}
