

@use 'fonts' as f;
@use 'colors' as c;

* {
    font-family: f.$font-standard;
}

div, span, p, a {
    line-height: 1.4;
    margin-top: 0;
}

h1, h2, h3, h4, h5, h6 {
    font-family: f.$font-headline;
    line-height: 1.4;
    margin-top: 0;
    text-decoration: none;
    color: c.$tb-green;

    a {
        text-decoration: none;
        color: inherit;
    }
}

h1 {
    margin-bottom: 1rem;
}

img {
    display: block;
    max-width: 100%;
}

.post-heading {
    margin-bottom: 1.4rem;
}

.post-date {
    color: c.$grey;
}

.tb-pagination {
    margin-left: auto;
    margin-top: 80px;
    width: 100%;
    text-align: center;
    color: c.$tb-green;

    a {
        color: inherit;
        text-decoration: none;
        font-family: f.$font-headline;
        font-size: 1.2rem;
        
        :hover {
            text-decoration: underline;
        }
    }
}