@use 'sass:list';

@font-face {
  font-family: 'Poppins';
  src:  url('../fonts/poppins/Poppins-Medium.ttf') format('truetype'),
}

@font-face {
  font-family: 'RobotoCondensed';
  src:  url('../fonts/roboto-condensed/RobotoCondensed-Regular.ttf') format('truetype'),
}

@font-face {
  font-family: 'Rajdhani';
  src:  url('../fonts/rajdhani/Rajdhani-SemiBold.ttf') format('truetype'),
}

@function with-fallback-fonts($fonts...) {

  @return list.append($fonts, (
    // Safari for OS X and iOS (San Francisco)
    -apple-system,
    // Chrome < 56 for OS X (San Francisco)
    BlinkMacSystemFont,
    // Windows
    "Segoe UI",
    // Android
    "Roboto",
    // Basic web fallback
    "Helvetica Neue", Arial, sans-serif,
    // Emoji fonts
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"
  ), $separator: comma);
}

$font-heavy: with-fallback-fonts("Rajdhani");
$font-headline: with-fallback-fonts("RobotoCondensed");
$font-standard: with-fallback-fonts("RobotoCondensed");
